/*------------------------------------*\
  popup
\*------------------------------------*/
.popup {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -2;
  background-color: rgba(0,0,0,0.4);
  opacity: 0;
  transition: opacity 0.4s;
  &-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2002;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    @include screen($width-mobile) {
      padding: 40px 20px;
    }
  }
  &-content {
    position: relative;
    flex: 0 0 auto;
    z-index: 2002;
    width: 100%;
    max-width: 700px;
    margin: 0 auto;
    padding: 20px 20px 40px;
    background: $white;
    border-radius: 20px;
    border: solid 1px #cfcfcf;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.25);
    &.w-wide {
      max-width: 900px;
    }
    &.type--inner {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: stretch;
      min-height: 200px;
      padding-top: 40px;
      .popup-area {
        width: 100%;
      }
      &.h-m {
        min-height: 340px;
      }
    }
    @include screen($width-mobile) {
      padding: 20px;
    }
  }
  &-area {
    display: flex;
    flex-direction: column;
    max-width: 560px;
    max-height: calc(100vh - 120px);
    margin-left: auto;
    margin-right: auto;
  }
  .w-wide .popup-area {
    max-width: 740px;
  }
  &.open {
    position: fixed;
    z-index: 2000;
    opacity: 1;
    overflow-y: auto;
  }
  &-close {
    display: block;
    position: absolute;
    top: -15px;
    right: -15px;
    z-index: 3;
    width: 50px;
    height: 50px;
    background: $color-main url(../images/popup-close.png);
    background-size: 100% auto;
    border-radius: 50%;
    @include transition(background-color);
    &:hover, &:focus {
      background-color: #941822;
    }
    @include screen($width-mobile) {
      width: 40px;
      height: 40px;
    }
  }
  &-head {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    min-height: 90px;
    padding: 20px;
    margin-bottom: 24px;
    border-bottom: solid 1px #e4e9ec;
    @include screen($width-mobile) {
      min-height: 60px;
      padding: 10px 0 20px;
      margin-bottom: 20px;
    }
  }
  &-title {
    flex: 1 1 auto;
    font-size: 28px;
    line-height: 30px;
    font-weight: 700;
    text-align: center;
    color: $color-main;
    @include screen($width-mobile) {
      font-size: 24px;
    }
  }
  &-inner {
    flex: 0 1 auto;
    overflow-y: auto;
  }
  &-foot {
    flex: 0 0 auto;
    padding-top: 24px;
  }
}

.popSlide {
  .popSlide-inner {
    background-color: #fff;
  }
}
.popup-foot .slideNav {
  .swiper-pagination {
    letter-spacing: 1px;
    margin: 0 50px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    border-radius: 50%;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
  }
  .swiper-button-disabled {
    opacity: 1;
  }
}



/*------------------------------------*\
  loading
\*------------------------------------*/
.loading {
  display: inline-block;
  width: 50px;
  height: 50px;
  background: url(../images/icon-loading.png) center center;
  background-size: 100% 100%;
  animation: load 3s linear infinite;
}
@keyframes load {
  0% { transform: rotate(360deg); }
  50% { transform: rotate(180deg); }
  100% { transform: rotate(0deg); }
}
.loadingBox {
  width: 120px;
  height: 120px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding: 35px;
  background-color: $bg-second;
  border-radius: 60px;
}


