/*------------------------------------*\
  天地框架及popup
\*------------------------------------*/
body {
  max-width: 100%;
}
.wrapper {
  padding-top: 100px;
  @include screen($width-mobile) {
    padding-top: 50px;
  }
}

.pagesize {
  margin-left: auto;
  margin-right: auto;
  max-width: $pagesize-max;
  padding: 0 $pagesize-padding;
  @include screen($width-large) {
    padding: 0 20px;
  }
}



/*------------------------------------*\
  header
\*------------------------------------*/
header {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  max-width: 100%;
}
.headerArea {
  position: relative;
  z-index: 100;
  background-color: #fff;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.1);
  @include screen($width-mobile) {
    z-index: 3;
  }
  & > .pagesize {
    display: flex;
    align-items: center;
    gap: 0 8px;
    height: 100px;
    @include screen($width-mobile) {
      height: 50px;
      padding: 0 0 0 50px;
      @include transition(padding);
    }
  }
}

.header {
  &-logo {
    &-img {
      display: block;
      width: 186px;
      height: 50px;
      background: url(../images/frame/header-logo.png) left center no-repeat;
      background-size: 100% auto;
      @include screen($width-mid) {
        width: 154px;
      }
      @include screen($width-mobile) {
        width: 110px;
        background-size: auto 25px;
        margin-left: 14px;
      }
    }
  }
  &-inner {
    flex: 1 1 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &-nav {
    display: flex;
    align-items: flex-end;
    gap: 0 24px;
    margin-right: 24px;
    @include screen($width-large) {
      gap: 0 16px;
      margin-right: 16px;
    }
    @include screen($width-mid) {
      gap: 0 4px;
      margin-right: 8px;
    }
    @include screen($width-mobile) {
      display: none;
    }
    a {
      display: block;
      position: relative;
      text-align: center;
      font-size: 20px;
      line-height: 24px;
      color: $text-dark;
      min-width: 2.5em;
      padding: 13px 3px;
      cursor: pointer;
      @include transition(color);
      @include screen($width-large) {
        font-size: 18px;
      }
      @include screen($width-mid) {
        font-size: 1rem;
      }
      &:hover, &:focus {
        color: $color-main;
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0px;
        background-color: $color-main;
        border-radius: 2px;
        @include transition(height);
      }
      &.open {
        color: $color-main;
        font-weight: 500;
        &::after {
          height: 4px;
        }
      }
      .word {
        display: inline-block;
      }
    }
  }
  &-btnBox {
    flex: 0 0 auto;
    display: flex;
    gap: 0 10px;
    @include screen($width-mid) {
      gap: 0 6px;
    }
    @include screen($width-mobile) {
      gap: 0 1px;
    }
  }
  &-btn {
    @extend %btnHover;
    color: #fff;
    background-color: $color-main;
    padding: 5px 17px;
    border-radius: 20px;
    @include screen($width-mid) {
      padding: 5px 10px;
    }
    @include screen($width-mobile) {
      font-weight: 500;
      color: $color-main;
      background-color: transparent;
      padding: 10px 8px;
    }
    &--add .text {
      padding-left: 36px;
      background: url(../images/frame/header-btn-add.png) left center no-repeat;
      background-size: auto 26px;
    }
    &--login .text {
      padding-left: 30px;
      background: url(../images/frame/header-btn-login.png) left center no-repeat;
      background-size: auto 26px;
    }
    .text {
      display: inline-block;
      font-size: 18px;
      line-height: 30px;
      letter-spacing: 1px;
      @include screen($width-mid) {
        font-size: 1rem;
        letter-spacing: 0;
      }
      @include screen($width-mobile) {
        font-size: 14px;
        padding-left: 0;
        background: none;
      }
    }
  }
}

.pageMenu-btn, .pageMenu-close {
  display: none;
}
@include screen($width-mobile) {
  .openmenu {
    .headerArea {
      background-color: $color-main;
      & > .pagesize {
        padding: 0 50px 0 0;
      }
    }
    .header-logo-img {
      background: url(../images/frame/header-logo-m.png) left center no-repeat;
      background-size: auto 25px;
    }
    .header-btnBox, .header-loginNav {
      display: none;
    }
  }
  .pageMenu-btn, .pageMenu-close {
    display: block;
  }
  .pageMenu-close {
    
  }
}

// 選單下拉
.headerHidden {
  display: none;
  position: absolute;
  z-index: 99;
  top: 100px;
  // left: 0;
  // width: 100%;
  transform: translateX(-50%);
  @include screen($width-mobile) {
    display: none;
  }
  &.open {
    // display: block;
  }
  &-inner {
  }
}
.headHidNav {
  // display: grid;
  // grid-template-columns: repeat(5, 1fr);
  // gap: 40px;
  // display: inline-block;
  
  width: auto;
  // min-width: 170px;
  max-height: calc(100vh - 150px);
  overflow-y: auto;
  padding: 30px 28px;
  border: solid 1px #f0f0f0;
  border-top: none;
  background-color: #fff;
  border-radius: 0 0 20px 20px;
  box-shadow: 0 14px 20px 0 rgba(100,100,100,0.1);
  @include screen($width-large) {
    // grid-template-columns: repeat(4, 1fr);
    padding: 20px 40px 30px;
  }
  @include screen($width-mid) {
    padding: 20px 20px 30px;
    // gap: 30px 20px;
  }
  .item {
    max-width: 200px;
    ul {
      padding-left: 0;
      list-style: none;
    }
    a {
      display: block;
      margin: 4px 0;
      color: $text-gray;
      @include transition(color);
      &:hover, &:focus {
        color: $color-main;
      }
      @include screen($width-large) {
        margin: 2px 0;
      }
    }
  }
  .title {
    font-size: 18px;
    line-height: 48px;
    font-weight: 500;
    color: $color-main;
    margin-bottom: 12px;
    border-bottom: solid 2px #ddd;
    @include screen($width-mid) {
      line-height: 40px;
    }
    .text {
      display: inline-block;
      position: relative;
      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        bottom: -2px;
        height: 2px;
        background-color: $color-main;
      }
    }
  }
  &.type--in {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 40px;
    max-height: revert;
    padding: 0;
    background-color: transparent;
    border: none;
    border-radius: 0;
    box-shadow: none;
    .item {
      max-width: 100%;
    }
    @include screen($width-xl) {
      grid-template-columns: repeat(4, 1fr);
    }
    @include screen($width-mid) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include screen($width-mobile) {
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }
}

// 登入選單
.header-loginNav {
  flex: 0 0 auto;
  position: relative;
  padding-right: 40px;
  background: url(../images/frame/login-arrow.png) right 10px center no-repeat;
  background-size: 15px auto;
  @include screen($width-mobile) {
    padding-right: 30px;
    background-size: 14px auto;
    background-position: right 8px center;
  }
  .loginNavBtn {
    position: relative;
    display: block;
    .text {
      display: inline-block;
      width: 40px;
      height: 40px;
      line-height: 40px;
      font-size: 18px;
      text-align: center;
      color: #fff;
      background-color: $color-main;
      border-radius: 20px 20px 0 20px;
      @include screen($width-mobile) {
        width: 30px;
        height: 30px;
        line-height: 30px;
        font-size: 15px;
        border-radius: 15px;
      }
    }
  }
}
.loginNavHidden {
  display: none;
  position: absolute;
  top: calc(100% + 10px);
  right: 40px;
  @include screen($width-mobile) {
    top: 40px;
    right: 10px;
    width: calc(100vw - 20px);
  }
  &-inner {
    color: #fff;
    padding: 24px 18px 16px;
    background-color: $color-main;
    border-radius: 20px 0 20px 20px;
    @include screen($width-mobile) {
      padding: 16px 24px;
      border-top-left-radius: 0;
    }
  }
  ul {
    padding-left: 0;
    list-style: none;
    a {
      display: block;
      margin: 4px 0;
      white-space: nowrap;
      @include transition(color);
      &:hover, &:focus {
        color: #ffef6b;
      }
      @include screen($width-mobile) {
        padding: 4px 0;
      }
    }
  }
}


/*------------------------------------*\
  footer
\*------------------------------------*/
// $footer-social-icon:('line', 'fb', 'yt');
// @each $name in $footer-social-icon {
