.foot-main {
  font-size: 15px;
  line-height: 20px;
  color: $color-second-dark;
  padding: 36px 60px;
  background: $bg-second;
  border-bottom: solid 6px $color-main;
  @include screen($width-mobile) {
    font-weight: 400;
    padding: 20px 0;
    border-bottom-width: 4px;
  }
  .pagesize {
  }
}
.foot {
  &-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 8px 30px;
    @include screen($width-mobile) {
      display: block;
    }
    p {
      padding: 5px 0;
      @include screen($width-mobile) {
        padding: 4px 0;
      }
    }
  }
  &-btn-social {
    flex: 0 0 auto;
    order: 3;
    display: flex;
    gap: 0 16px;
  }
}

.footNav {
  &-main {
    color: #fff;
    background-color: $color-main;
    .pagesize {
      display: flex;
      // justify-content: center;
      gap: 0 30px;
      @include screen($width-xl) {
        justify-content: space-between;
        gap: 0 16px;
      }
      @include screen($width-large) {
        gap: 0 8px;
      }
      @include screen($width-mid) {
        gap: 0 5px;
      }
    }
    a {
      display: block;
      text-align: center;
      font-size: 17px;
      font-weight: 500;
      padding: 28px 0;
      @include screen($width-large) {
        flex: 1 1 auto;
        padding: 16px 0;
      }
      @include screen($width-mid) {
        font-size: 15px;
        letter-spacing: -0.1px;
      }
    }
    .word {
      display: inline-block;
    }
  }
  &-second {
    display: none;
    background-color: $color-second-light;
    .pagesize {
      display: flex;
      flex-wrap: wrap;
      // justify-content: center;
      gap: 0 30px;
    }
    a {
      padding: 13px 0;
      color: #fff;
      display: inline-block;
    }
  }
  .lv1 {
    position: relative;
    overflow: hidden;
    &::after {
      content: "";
      position: absolute;
      bottom: -16px;
      left: calc(50% - 26px);
      width: 0px;
      height: 0px;
      border-style: solid;
      border-width: 0 14px 16px 14px;
      border-color: transparent transparent $color-second-light transparent;
      transform: rotate(0deg);
      @include transition(bottom);
      @include screen($width-large) {
        left: calc(50% - 20px);
        border-width: 0 8px 10px 8px;
      }
      @include screen($width-mid) {
        left: calc(50% - 13px);
      }
    }
    .text {
      display: inline-block;
      width: auto;
      position: relative;
      padding-right: 20px;
      @include screen($width-mid) {
        padding-right: 12px;
      }
      &::after {
        content: "";
        position: absolute;
        top: calc(50% - 6px);
        right: 4px;
        width: 0px;
        height: 0px;
        border-style: solid;
        border-width: 0 6px 12px 6px;
        border-color: transparent transparent #fff transparent;
        transform: rotate(0deg);
        @include transition;
        @include screen($width-mid) {
          right: 0px;
          border-width: 0 5px 10px 5px;
        }
      }
    }
    &.open {
      &::after {
        bottom: 0px;
      }
      .text::after {
        transform: rotateX(180deg);
      }
    }
  }
}

@include screen($width-mobile) {
  .footNav-main, 
  .footNav-second {
    display: none;
  }
}

.footNav--fold {
  display: none;
  color: #fff;
  background-color: $color-main;
  .folder--1 .fold-btn {
    position: relative;
    overflow: hidden;
    display: block;
    font-size: 18px;
    padding: 13px 30px 13px 20px;
    border-bottom: solid 1px #d98087;
    &::before {
      content: "";
      display: block;
      position: absolute;
      right: 26px;
      top: calc(50% - 7px);
      width: 0px;
      height: 0px;
      border-style: solid;
      border-width: 0 8px 14px 8px;
      border-color: transparent transparent #fff transparent;
      @include transition(transform);
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 18px;
      width: 0px;
      height: 0px;
      border-style: solid;
      border-width: 0 8px 10px 8px;
      border-color: transparent transparent #747f86 transparent;
      @include transition(bottom);
    }
  }
  .inner--1 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 6px;
    font-size: 18px;
    padding: 12px 20px;
    @include screen($width-small) {
      grid-template-columns: repeat(2, 1fr);
    }
    a {
      padding: 5px 0;
    }
  }
  .folder--2 {
    background-color: $color-second-light;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    gap: 6px 20px;
    a {
      padding: 2px 0;
    }
  }
  .folder--1.open {
    .fold-btn {
      background-color: #941822;
      border-color: #747f86;
      &::before {
        transform: scaleY(-1);
      }
      &::after {
        bottom: 0;
      }
    }
    .folder--2 {
      position: relative;
    }
  }
  @include screen($width-mobile) {
    display: block;
  }
}



/*------------------------------------*\
  floatBtn 右側浮動按鈕
\*------------------------------------*/
$floatNav-width: 80px;
$floatNav-maxsize: $pagesize-max + $floatNav-width + 160px;
.floatNav {
  position: fixed;
  z-index: 900;
  left: 0;
  bottom: 30px;
  width: 100%;
  height: 0;
  overflow: visible;
  display: none;
  @include screen($width-mobile) {
    bottom: 14px;
  }
  &-inner {
    position: relative;
    // right: calc(-60px - $floatNav-width);
    right: 10px;

    width: $floatNav-width;
    @media all and (max-width: $floatNav-maxsize) {
      right: 0;
    }
    @include screen($width-mid) {
      width: 80px;
    }
    @include screen($width-mobile) {
      width: 50px;
    }
  }
  .pagesize {
    // max-width: $pagesize-width;
    max-width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;
    height: 0;
    padding: 0;
    overflow: visible;
    @media all and (max-width: $floatNav-maxsize) {
      max-width: 100%;
    }
  }
  a {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  &-top {
    display: block;
    width: 40px;
    height: 40px;
    background: url(../images/floatNav-top.png) center center no-repeat;
    background-size: 100% 100%;
    border-radius: 20px;
    margin: 0 auto;
    @extend %btnHover;
    @include screen($width-mobile) {
      width: 32px;
      height: 32px;
    }
  }
  .btn-service {
    width: 80px;
    height: 80px;
    background: url(../images/floatNav-service.png) center center no-repeat;
    background-size: contain;
    margin-bottom: 10px;
    @include screen($width-mid) {
      width: 80px;
      height: 80px;
      margin-bottom: 10px;
    }
    @include screen($width-mobile) {
      width: 50px;
      height: 50px;
      // margin-bottom: 12px;
      margin-bottom: 8px;

    }
  }
}


